            <p class="sixtext1" data-60000="top: -100%; display: block;" data-60500="top: 20%; display: block;" data-60800="top: 20%; display: block;" data-60801="top: 20%; display: none;" style="top: 20%; display: block;">所有勒索者的共通特質是<highlight>長期感到憂慮及匱乏、喪失安全感及自信心</highlight>，為了抵禦不確定感和壓力的侵蝕，進而衍生出勒索的手段。</p>
