            <p class="sixtext1" data-61200="top: 18%; display: none;" data-61300="top: 25%; display: block;" data-61600="top: 25%; display: block;" data-61601="top: 18%; display: none;" style="top: 25%; display: none;">情緒勒索你情我願，如果沒有你的默許，這場交易不會完成！</p>
            <p class="sixtext1" data-61600="top: 18%; display: none;" data-61700="top: 20%; display: block;" data-62000="top: 20%; display: block;" data-62001="top: 18%; display: none;" style="top: 20%; display: none;" >被勒索者不夠愛自己、不夠重視自己的感受、總是害怕會失去關係…，而努力維持關係、希望可以換到對方的看重和愛，殊不知，長期的勒索反倒換來了害怕與傷害。</p>
            <p class="sixtext1" data-62000="top: 18%; display: none;" data-62100="top: 25%; display: block;" data-62400="top: 25%; display: block;" data-62401="top: 18%; display: none;" style="top: 25%; display: none;">這兩大類的特質有個共通性---<highlight>脆弱的自我</highlight></p>
            <p class="sixtext1" data-62400="top: 18%; display: none;" data-62500="top: 20%; display: block;" data-62800="top: 20%; display: block;" data-62801="top: 18%; display: none;" style="top: 20%; display: none;">基於種種相似的核心特質，勒索者與被勒索者的攻守位置其實是會交換的！很多人有時在一段關係中扮演勒索者的角色，而在另一段關係中卻扮演被勒索者的角色。</p>
            <p class="sixtext1" data-62800="top: 18%; display: none;" data-62900="top: 20%; display: block;" data-63500="top: 20%; display: block;" data-64000="top: -100%; display: none;" style="top: 20%; display: none;">又或者，一段關係中的兩人互相扮演著勒索者與被勒索者，彼此相互勒索。這些情況都是可能的。</p>
